            flex: 1;
}
.skillArea .step2,
  .skillArea .step3 {
    -webkit-box-flex: 2;
        -ms-flex: 2;
            flex: 2;
}
.eachSkill {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
.eachSkill > * {
    opacity: 0.3;
}
.eachSkill.clickable > * {
    opacity: 1;
}
.progress-arrow {
  position: absolute;
  width: 0;
  height: 0;
  border-width: 15px 40px 0 40px;
  border-style: solid;
  border-color: #35006A transparent transparent transparent;
  opacity: 0.7 !important;
  top: calc(100% - 15px);
  left: 10px;
}
.progress-rect {
  opacity: 0.7 !important;
  position: absolute;
  top: 86px;
  left: 30px;
  width: 40px;
  height: calc(100% - 101px);
  background-color: #35006A;
}
.clickMask {
  position: absolute;
  background-color: transparent;
  left: 0;
  top: 0;
  z-index: 10;
  height: 100%;
  width: 100%;
}
.progress-img {
  text-align: center;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.skill-body {
  -webkit-box-flex: 3;
      -ms-flex: 3;
          flex: 3;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 10px 15px;
}
.skill-detail {
  width: 65px;
  text-align: center;
}
.skill-detail .detail-img {
    font-size: 40px;
    cursor: pointer;
}
.skill-detail .detail-img .img {
      -webkit-transition: all 0.1s ease-in;
      transition: all 0.1s ease-in;
      background-color: #35006A;
      padding: 10px;
      position: relative;
}
.skill-detail .detail-img .completedTri {
      position: absolute;
      top: 0;
      right: 0;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 16px 16px 0;
      border-color: transparent #6900D1 transparent transparent;
}
.skill-detail.isActive .img {
    border: 1px solid #FF0093;
    background-color: #6900D1;
}
.skill-detail.isPartCompleted .img {
    color: #8000FF;
}
.detail-list {
  margin-top: 13px;
  font-size: 14px;
}
.noteArea {
  padding: 8px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.note-container {
