}
.eachSelect {
  cursor: pointer;
  margin: 5px auto;
  width: 70%;
  padding: 8px 10px;
  border: 2px solid #fff;
  -webkit-transition: all 0.1s ease-in;
  transition: all 0.1s ease-in;
}
.eachSelect:hover, .eachSelect.checked {
    background-color: #FF0093;
    border: 2px solid #FF0093;
    color: #fff;
}
